// Displays a panel, an organisation unit in TypeDoc used to group single entities
// like a method or a variable.
//
// <div class="tsd-panel">
//   <h3>Eirmod tempor invidunt</h3>
//   <p>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat.</p>
// </div>
//
.tsd-panel {
    @extend %prevent-children-margin;
    padding: 24px;

    &:empty {
        display: none; }

    > h1, > h2, > h3 {
        color: #282828;
        font-size: 1rem;
        font-weight: 400;
        padding: 0 0 8px;
        border-bottom: 1px solid #ededed;

        &.tsd-before-signature {
            margin-bottom: 0;
            border-bottom: 0; } }

    table {
        display: block;
        width: 100%;
        overflow: auto;
        margin-top: 10px;
        word-break: normal;
        word-break: keep-all;

        th {
            font-weight: bold; }

        th, td {
            padding: 6px 13px;
            border: 1px solid #ddd; }

        tr {
            background-color: #fff;
            border-top: 1px solid #ccc;

            &:nth-child(2n) {
                background-color: #f8f8f8; } } } }


// Holds a series of panels with an optional heading.
//
// <div class="tsd-panel-group">
//   <h2>Consetetur sadipscing elitr</h2>
//   <div class="tsd-panel">
//     <h3>Eirmod tempor invidunt</h3>
//     <p>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat.</p>
//   </div>
//   <div class="tsd-panel">
//     <h3>Eirmod tempor invidunt</h3>
//     <p>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat.</p>
//   </div>
// </div>
//
.tsd-panel-group {
    margin: 0;

    > h1, > h2, > h3 {
        color: #282828;
        font-size: 2rem;
        font-weight: 300;
        line-height: 3rem;
        margin-bottom: 0;
        padding: 0 24px; } }
