pre {
    position: relative;
    opacity: 1;
    background: #fff;
    color: #777;
    border: 1px solid #ededed;
    box-shadow: none;

    &:hover {
        box-shadow: 0 0 8px 0 rgba(0, 0, 0, .15); }

    + pre {
        margin-top: 10px; } }


.hljs {
    display: block;
    overflow-x: auto;
    background: inherit;
    position: relative; }


.hljs-lang-name {
    position: absolute;
    padding: 8px;
    top: 0;
    right: 0;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    opacity: .7; }


.hljs-code-copy {
    position: absolute;
    display: flex;
    align-items: center;
    padding: 8px;
    font-size: 12px;
    color: transparentize(#fff, .4);
    background: #09f;
    font-weight: 600;
    border: 0;
    top: 0;
    right: 0;
    outline-style: none;
    transition: color .25s ease-in-out;
    &:hover {
        color: #fff; }

    &::before {
        font-family: 'Material Icons', sans-serif;
        font-size: inherit;
        content: '\E14D';
        color: inherit;
        margin-right: 8px; }

    &.hidden {
        display: none; } }

.hljs-comment,
.hljs-quote {
    color: #5c6370;
    font-style: italic; }


.hljs-doctag,
.hljs-keyword,
.hljs-formula {
    color: #fb9a00; }


.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
    color: #e06c75; }


.hljs-literal {
    color: #e32659; }


.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
    color: #68b400; }


.hljs-built_in,
.hljs-class .hljs-title {
    color: #dc65ff; }


.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
    color: #d19a66; }


.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
    color: #0072be; }


.hljs-emphasis {
    font-style: italic; }


.hljs-strong {
    font-weight: 600; }


.hljs-link {
    text-decoration: underline; }
