.tsd-index-panel {
    .tsd-index-content {
        margin-bottom: -30px !important; }

    .tsd-index-section {
        font-size: 15px;
        margin-bottom: 30px !important; }

    h3 {
        color: #000;
        font-size: 1rem;
        font-weight: 400;
        padding: 0 0 8px;
        border-bottom: 1px solid #ededed; }

    ul.tsd-index-list {
        @include vendors(column-count, 3);
        @include vendors(column-gap, 20px);
        padding: 0;
        list-style: none;
        line-height: $LINE_HEIGHT;

        @include size-xs-sm {
            @include vendors(column-count, 1); }

        @include size-md {
            @include vendors(column-count, 2); }

        li {
            @include vendors(column-break-inside, avoid);
            @include vendors(page-break-inside, avoid); } }

    a,
    .tsd-parent-kind-module a {
        color: $COLOR_TS; }

    .tsd-parent-kind-interface a {
        color: $COLOR_TS_INTERFACE; }

    .tsd-parent-kind-enum a {
        color: $COLOR_TS_ENUM; }

    .tsd-parent-kind-class a {
        color: $COLOR_TS_CLASS; }


    .tsd-kind-module a {
        color: $COLOR_TS; }

    .tsd-kind-interface a {
        color: $COLOR_TS_INTERFACE; }

    .tsd-kind-enum a {
        color: $COLOR_TS_ENUM; }

    .tsd-kind-class a {
        color: $COLOR_TS_CLASS; }

    .tsd-is-private a {
        color: $COLOR_TS_PRIVATE; } }
