// Displays the toolbar at the top of the page.
//
// <div class="tsd-page-toolbar">
//   <div class="container">
//     <div class="table-wrap">
//       <div class="table-cell">
//         <a href="../index.html" class="title">TypeDoc Documentation</a>
//       </div>
//     </div>
//   </div>
// </div>
//
.tsd-page-toolbar {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    color: $TOOLBAR_TEXT_COLOR;

    a {
        color: $TOOLBAR_TEXT_COLOR;
        text-decoration: none;

        &.title {
            font-weight: bold; }

        &.title:hover {
            text-decoration: underline; } }

    .table-wrap {
        display: table;
        width: 100%;
        height: $TOOLBAR_HEIGHT; }

    .table-cell {
        display: table-cell;
        position: relative;
        white-space: nowrap;
        line-height: $TOOLBAR_HEIGHT;

        &:first-child {
            width: 100%; } } }

%TSD_WIDGET_ICON {
    &::before {
        content: '';
        display: inline-block;
        width: 40px;
        height: 40px;
        margin: 0 -5px 0 0;
        background-image: url(../images/widgets.png);
        background-repeat: no-repeat;
        text-indent: -1024px;
        vertical-align: bottom;

        @include retina {
            background-image: url(../images/widgets@2x.png);
            background-size: 320px 40px; } } }

%TSD_WIDGET_ICON-MATERIAL {
    &::before {
        display: inline-flex;
        content: '';
        justify-content: center;
        align-items: center;
        font-family: 'Material Icons';
        font-size: 18px;
        width: 24px;
        height: 14px;
        margin-right: 4px;
        color: #09f; } }

.tsd-widget {
    @extend %TSD_WIDGET_ICON-MATERIAL;
    display: inline-block;
    overflow: hidden;
    font-size: 14px;
    cursor: pointer;
    user-select: none;
    padding: 5px 0;

    &.active {
        opacity: 1;
        background-color: $COLOR_PANEL_DIVIDER; }

    &.no-caption {
        width: 40px;

        &:before {
            margin: 0; } }

    &.search:before {
        background-position: 0 0; }

    &.menu:before {
        background-position: -40px 0; }

    &.options:before {
        background-position: -80px 0; }

    &.options,
    &.menu {
        display: none;

        @include size-xs-sm {
            display: inline-block; } }

    input[type=checkbox] + &:before {
        color: #484848;
        content: '\e835'; }

    input[type=checkbox]:checked + &:before {
        color: #09f;
        content: '\e834'; } }

.tsd-select {
    position: relative;
    display: inline-block;
    transition: opacity 0.1s, background-color 0.2s;
    vertical-align: bottom;
    cursor: pointer;
    z-index: 2;

    .tsd-select-label {
        @extend %TSD_WIDGET_ICON-MATERIAL;
        padding: 5px 0;
        &::before {
            content: '\e8f4'; } }

    &.active {
        .tsd-select-label {
            position: relative; }

        .tsd-select-list {
            visibility: visible;
            opacity: 1;
            transition-delay: 0s; } }

    .tsd-select-list {
        position: absolute;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        margin: 0;
        padding: 0;
        opacity: 0;
        list-style: none;
        transition: visibility 0s 0.2s, opacity 0.2s;
        border: 1px solid #dedede;

        li {
            @extend %TSD_WIDGET_ICON-MATERIAL;
            padding: 5px 0;
            background-color: $COLOR_BACKGROUND;

            &:nth-child(even) {
                background-color: $COLOR_PANEL; }

            &:hover {
                background-color: $COLOR_PANEL_DIVIDER; }

            &.selected:before {
                font-size: 14px;
                content: '\e5ca'; } } }

    @include size-xs-sm {
        .tsd-select-list {
            top: 0;
            left: auto;
            right: 100%;
            margin-right: -5px; }

        .tsd-select-label:before {
            background-position: -280px 0; } } }
